body {
    margin: 0;
    padding: 0;
    color: #86d1da;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
}

div.board {
    background-color: brown;
    width: 100%;
    background: #1fb0c4;
    background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(57%, #1fb0c4), color-stop(99%, #037275));
    background: -webkit-radial-gradient(center, ellipse cover, #1fb0c4 57%, #037275 99%);
    background: -o-radial-gradient(center, ellipse cover, #1fb0c4 57%, #037275 99%);
    background: -webkit-radial-gradient(center, ellipse, #1fb0c4 57%, #037275 99%);
    background: -o-radial-gradient(center, ellipse, #1fb0c4 57%, #037275 99%);
    background: radial-gradient(ellipse at center, #1fb0c4 57%, #037275 99%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#1fb0c4', endColorstr='#037275', GradientType=1);
    text-align: center;
  }

.small {
  max-width: 400px;
}

.large{
  max-width: 500px;
}

div.ticket {
    margin: 2px 10px;
    position: relative;
    display: inline-block;
    padding: 10px;
    background-color: rgba(0,0,0,0.5);
    border-radius: 10px;
}

.ticket:nth-child(even) {
  -webkit-transform: rotate(-3deg);
      -ms-transform: rotate(-3deg);
          transform: rotate(-3deg);
}

.ticket:nth-child(odd) {
  -webkit-transform: rotate(3deg);
      -ms-transform: rotate(3deg);
          transform: rotate(3deg);
}

div.ticket img {
  max-width: 100%;
}

dt, dd {
    float: left
}

dt {
  font-weight: bold;
  display: block;
  width: 20px;
  clear: both;
  color: #daf5f8;
}





